/*  2 Header bottom area
/*----------------------------------------*/
.header-one {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    z-index: 999;
}

.logo img {
    position: relative;
    z-index: 2;
}

.menu-bg {
