    font-size: 15px;
    text-transform: uppercase;
}

.header_menu ul li a:hover {
    color: #FF06B7 !important;
}

.header_menu ul li.menu-item-has-children>a::after {
    content: " +";
}

.header_menu ul li.menu-item-has-children>a::after {
    content: '\e64b';
    font-family: 'themify';
    font-weight: 700;
    margin-left: 5px;
    font-size: 10px;
}

.mean-container .mean-nav {
    background: #131740;
    float: left;
    width: 100%;
}

.header_menu ul li ul.submenu {
    position: absolute;
    background: #131740;
    width: 230px;
    top: 110%;
    left: 0;
    opacity: 0;
    visibility: hidden;
    padding: 15px 0;
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -ms-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.05);
    z-index: 99;
    text-align: left;
}

.header_menu ul li ul.submenu li {
    margin: 0;
    display: block;
}

.header_menu ul li ul.submenu li a {
    padding: 12px 30px;
    font-size: 14px;
    position: relative;
    display: block;
}

.header_menu ul li ul.submenu li a:hover {
    color: #FF06B7;
    background: #232861;
}

.header_menu ul li:hover ul.submenu {
    opacity: 1;
    visibility: visible;
    top: 96%;
}

.header_menu ul li ul.submenu li:hover>a {
    color: #fff;
}

.header-social-icons {
